.hero {
    padding: 0 $gap;
    margin: 50px 0 percent(182, 1366);

    @include mq-down(md) {
        display: flex;
        flex-direction: column-reverse;
        margin: 0 0 percent(90, 768);
        padding-bottom: percent(80, 768);
        position: relative;

        &:after {
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            border-top: 2px solid;
        }
    }

    @include mq-down(sm) {
        margin-bottom: percent(70, 375);
        padding-bottom: percent(90, 375);
    }

    &.is-home {
        margin: 0;
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        padding-right: 0;

        @include mq-down(xl) {
            margin-bottom: percent(67, 1337);
        }

        @include mq-down(md) {
            margin-bottom: percent(145, 768);
            flex-direction: column;
            padding: 0;
            display: block;

            &:after {
                display: none;
            }
        }

        @include mq-down(sm) {
            margin-bottom: percent(83, 375);
        }

        .infos {
            flex-direction: column;
            justify-content: flex-start;
            padding: 18vh 0 0 0;
            max-width: 600px;

            @include mq-down(lg) {
                padding-top: 9vh;
            }

            @include mq-down(md) {
                margin-bottom: 50px;
                padding: 0 $gap;
            }
        }

        .title {
            margin: 0 0 2.825em 0;
        }

        .text {
            padding-top: 0;
            max-width: none;

            @include mq-down(md) {
                column-count: 1;
                column-gap: 0;
                max-width: 600px;
            }
        }

        .img {
            min-width: percent(1111, 1800);
            max-width: percent(1111, 1800);
            margin-left: percent(118, 1800);
            border-right: 0;

            @include mq-down(xl) {
                min-width: percent(846, 1321);
                max-width: percent(846, 1321);
                margin-left: percent(69, 1321);
            }

            @include mq-down(md) {
                min-width: 0;
                max-width: none;
                width: 100%;
                border-left: 0;
                border-right: 0;
                margin: 0;
            }

            &:before {
                padding-top: percent(500, 1111);

                @include mq-down(xl) {
                    padding-top: percent(475, 846);
                }

                @include mq-down(md) {
                    padding-top: percent(423, 768);
                }

                @include mq-down(sm) {
                    padding-top: percent(403, 375);
                }
            }
        }
    }

    .has-scroll-init & {
        &.is-home {
            .img {
            }
        }

        &.is-inview {
            &.is-home {
                .img {
                    &:before {
                    }
                }
            }

            .title {
                transform: none;
            }

            .text {
                opacity: 1;
            }

            .img {
                opacity: 1;
                transform: none;

                &:before {
                }
            }

            &:after {
                width: 100%;
            }
        }

        .title-wrapper {
            overflow: hidden;
        }

        .title {
            transition: all 1s 1s;
            transform: translateY(200%);
        }

        .text {
            transition: all 1s 1.5s;
            opacity: 0;
        }

        .img {
            transition: all 1s 1.75s;
            transform: translateY(100%);
            opacity: 0;

            &:before {
            }
        }

        &:after {
            transition: all 3s 2s;
            width: 0;
        }
    }

    .infos {
        padding: 77px 0 100px;
        display: flex;
        justify-content: space-between;

        @include mq-down(md) {
            padding: 0;
            display: block;
        }
    }

    .title-wrapper {
        overflow: hidden;
    }

    .title {
        font-size: 8rem;
        margin: 0 1em 1.5em 0;

        @include mq-down(md) {
            margin: 0 0 2.825em 0;
        }

        @include mq-down(sm) {
            font-size: 5rem;
        }
    }

    .text {
        font-size: 1.6rem;
        max-width: percent(517, 1280);
        padding-top: 1.5em;

        @include mq-down(md) {
            padding-top: 0;
            max-width: none;
            column-count: 2;
            column-gap: em(24, 16);
        }

        @include mq-down(sm) {
            column-count: 1;
            column-gap: 0;
        }
    }

    .img {
        border: 3px solid;
        width: 100%;
        position: relative;
        overflow: hidden;
        line-height: 0;

        @include mq-down(md) {
            min-width: 0;
            max-width: 100%;
            width: 100%;
            display: block;
            border-right: 3px solid;
            margin: 0 0 percent(79, 688);
        }

        @include mq-down(sm) {
            margin-bottom: percent(28, 330);
        }

        &:before {
            content: "";
            display: block;
            padding-top: percent(589, 1280);

            @include mq-down(md) {
                padding-top: percent(492, 688);
            }

            @include mq-down(sm) {
                padding-top: percent(454, 330);
            }
        }

        img {
            object-fit: cover;
            position: absolute;
            top: -15%;
            left: 0;
            height: 130%;
            width: 100%;
        }
    }
}



